<template>
  <div>
    <el-main>
      <el-table :data="tableData" height="510">
        <el-table-column prop="DeviceOwner" label="Device Owner" width="120"></el-table-column>
        <el-table-column prop="CostCenter" label="Cost Center" width="120"></el-table-column>
        <el-table-column prop="Brand" label="Manufacturer" width="120"></el-table-column>
        <el-table-column prop="SerialNumber" label="Serial N" width="120"></el-table-column>
        <el-table-column prop="MachineName" label="Machine Name" width="120"></el-table-column>
        <el-table-column prop="RegionValue" label="Region" width="120"></el-table-column>
        <el-table-column prop="CountryLocationValue" label="Country Locaion" width="120"></el-table-column>
        <el-table-column prop="SiteNameValue" label="Site Name" width="120"></el-table-column>
        <el-table-column prop="BusinessCategoryValue" label="Business Category" width="120"></el-table-column>
        <el-table-column prop="BusinessUnitValue" label="Business Unit" width="120"></el-table-column>
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="handleEdit(scope.row)" type="text" size="small">Edit</el-button>
            <el-button @click="handledelete(scope.row)" type="text" size="small">Delete</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      tableData: []
    };
  },
  components: {},
  created() {
    this.$axios
      .get("users")
      .then(res => {
        this.tableData = res.data;
      })
      .catch(function(error) {
        console.log("error!");
      });
  },
  methods: {
    handleEdit(row) {
      console.log(row);
    },
    handledelete(row) {
      console.log(row);
    }
  }
};
</script>

<style></style>



