<template>
  <div>
    <el-menu
      :default-openeds="['1']"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      :router="true"
      :default-active="menuselected"
    >
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>导航一</span>
        </template>
        <el-menu-item
          v-for="(item, index) in list"
          :key="item.title"
          :index="item.path"
          @click="tabClass(index)"
        >{{ item.title }}</el-menu-item>
      </el-submenu>

      <!-- el-submenu index="1">
        <template slot="title">
          <i class="el-icon-message"></i>导航一
        </template>
        <el-menu-item-group >
            <el-menu-item v-for="item in list" v-bind:key="item.title">
                <router-link :to="item.path">{{item.title}}</router-link>
            </el-menu-item>
        </el-menu-item-group>
      </el-submenu-->
    </el-menu>
  </div>
</template>

<script>
export default {
  name: "Side",
  data() {
    return {
      list: [
        {
          title: "Home",
          path: "/index"
        },
        {
          title: "Introduction",
          path: "/introduction"
        },
        {
          title: "Note",
          path: "/note"
        }
      ],
      menuselected: "/index"
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    tabClass(index) {}
  },
  components: {},
  created() {
    this.menuselected = this.list.find(element => {
      return element.path.endsWith(this.$route.name);
    }).path;
  }
};
</script>

<style></style>



