import Vue from 'vue'
import Router from 'vue-router'
import Header from '@/components/Header'
import Side from '@/components/Side'
import Home from '@/components/Home'
import Introduction from '@/components/Introduction'
import Note from '@/components/Note'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            redirect: '/index',
        },
        {
            path: '/index',
            name: 'index',
            components: {
                Header: Header,
                Side: Side,
                Home: Home,
            },
        },
        {
            path: '/introduction',
            name: 'introduction',
            components: {
                Header: Header,
                Side: Side,
                Home: Introduction,
            },
        },
        {
            path: '/note',
            name: 'note',
            components: {
                Header: Header,
                Side: Side,
                Home: Note,
            },
        },
    ],
})



